setInterval(function(){
    var data = new Date();

    var year =data.getFullYear();
    var month = data.getMonth()+1;
    var day = data.getDate();
    document.getElementById('year').innerHTML = year+'年'+month+'月'+day+'日';

    var weekdays = data.getDay();
    var week = '';
    switch(weekdays)
    {
      case 1:
          week = "星期一"
          break;
      case 2:
          week = "星期二"
          break;
      case 3:
          week = "星期三"
          break;
      case 4:
          week = "星期四"
          break;
      case 5:
          week = "星期五"
          break;
      case 6:
          week = "星期六"
          break;
      case 0:
          week = "星期日"
          break;
    };
    document.getElementById('weekday').innerHTML = week;

    var hours = data.getHours();
        if (hours<10) {
          hours = "0"+ hours;
        }
    var minutes = data.getMinutes();
        if (minutes<10) {
              minutes = "0"+ minutes;
        }
    var seconds = data.getSeconds();
        if (seconds<10) {
          seconds = "0"+seconds;
        }
    document.getElementById('second').innerHTML = hours+':'+minutes+':'+seconds
}, 1000);


